.timer {
    background: #222222;
    border-radius: var(--grid);
    margin: calc(var(--grid) * 2);
    display: grid;
    text-align: center;
    -webkit-align-content: center;
            align-content: center;
    font-size: 150px;
}

@media (min-width: 400px) {
    .timer {
        font-size: 200px;
    }
}

@media (min-width: 1024px) {
    .timer {
        font-size: 250px;
    }
}

.timer--active {
    background: rgb(197, 36, 36);
}

.timer:first-child span {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.App {
    display: grid;
    grid-template-rows: 1fr auto 1fr;
    height: 100vh;
}

.controls {
    -webkit-align-self: center;
            align-self: center;
    justify-self: center;
}

.controls button {
    font-size: 20px;
    height: calc(var(--grid) * 4);
    line-height: calc(var(--grid) * 4);
    border: 1px solid red;
    background: #272727;
    padding: 0 calc(var(--grid) * 1.5);
    border-radius: calc(var(--grid) / 2);
    margin: 0 var(--grid);
}

@media (min-width: 350px) {
    .controls button {
        padding: 0 calc(var(--grid) * 2);
        font-size: 24px;
        height: calc(var(--grid) * 4.5);
        line-height: calc(var(--grid) * 4.5);
    }
}

@media (min-width: 700px) {
    .controls button {
        padding: 0 calc(var(--grid) * 4);
        font-size: 32px;
        height: calc(var(--grid) * 6);
        line-height: calc(var(--grid) * 6);
    }
}

:root {
    --grid: 8px;
}

body {
    box-sizing: border-box;
    background: #000000;
    color: #ffffff;
    margin: 0;
    padding: 0;
    font-family: 'Teko', -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
        "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

* {
    font-family: inherit;
}

button {
    color: inherit;
    border: 0;
    outline: none;
    cursor: pointer;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

